.nice-select {
  width: auto;
  display: flex;
  align-items: center;
  line-height: 1;
  box-shadow: 3px 3px 10px 3px rgba(255, 82, 1, 0.05);
}

.nice-select:focus {
  border-color: rgba(255, 200, 39, 0.7);
}

.nice-select .list {
  max-height: 150px;
  overflow-y: auto;
  overflow-x: auto;
}

.nice-select .list li {
  min-height: 20px;
  max-height: 35px;
  display: flex;
  align-items: center;
}

.nice-select .list li::before {
  display: none;
}

.rtl {
  direction: rtl !important;
}

.form--check {
  display: flex;
  align-items: center;
}

.form--check label {
  line-height: 1;
  padding-left: 5px;
  font-size: 15px !important;
  cursor: pointer;
}

.form--check input[type="checkbox"] {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  height: 15px;
  width: 15px;
  cursor: pointer;
  background: #fff;
  border-radius: 3px;
  display: inline-block;
  position: relative;
}

.form--check input[type="checkbox"]::after {
  position: absolute;
  font-weight: 900;
  font-size: 16px;
  font-family: "Font Awesome 5 Free";
  content: "\f00c";
  font-size: 10px;
  color: #fff;
  visibility: hidden;
  background: #ffc827;
  line-height: 1;
  border-radius: 3px;
  width: 15px;
  height: 15px;
  left: 0;
  top: 0;
  text-align: center;
  line-height: 1.4;
}

.form--check input[type="checkbox"]:checked {
  border: none !important;
}

.form--check input[type="checkbox"]:checked::after {
  visibility: visible;
}

.custom--radio {
  display: flex;
}

.custom--radio label {
  margin-bottom: -2px;
  padding-left: 7px;
  font-size: 16px !important;
}

.custom--radio input[type="radio"] {
  appearance: none;
  padding: 0 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15px;
  width: 15px;
  cursor: pointer;
  background: #fff;
  border: 1px solid rgba(255, 82, 1, 0.4) !important;
  border-radius: 2px;
  margin-top: 4px;
  border-radius: 2px;
}

.custom--radio input[type="radio"]:checked {
  background: transparent;
  border-color: transparent !important;
}

.custom--radio input[type="radio"]::after {
  position: absolute;
  font-weight: 900;
  font-size: 16px;
  font-family: "Line Awesome Free";
  content: "\f14a";
  font-size: 18px;
  color: #ffc827;
  visibility: hidden;
  opacity: 0;
}

.custom--radio input[type="radio"]:checked::after {
  visibility: visible;
  opacity: 1;
}

.scrollToTop {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  z-index: 9999;
  bottom: 30px;
  right: 30px;
  font-size: 20px;
  position: fixed;
  -webkit-transform: translateY(150px);
  -ms-transform: translateY(150px);
  transform: translateY(150px);
  background: #ffc827;
  border-radius: 3px;
}

.scrollToTop:hover {
  color: #fff;
}

.scrollToTop.active {
  transform: translateY(0);
  animation: bounceInDown 2s;
}

.scrollToTop i {
  transform: rotate(-45deg);
}

