.cmn--btn {
  position: relative;
  overflow: hidden;
}

.cmn--btn::before,
.cmn--btn::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  z-index: -1;
  top: 0;
  -webkit-transition: all ease 0.3s;
  -moz-transition: all ease 0.3s;
  transition: all ease 0.3s;
}

.cmn--btn::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  top: -5px;
  background: #ffc827;
}

.cmn--btn:hover::before,
.cmn--btn.active::before {
  top: -100%;
}

