.video-button::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  border-radius: 50%;
  animation: play-button 2s linear infinite;
}

.video-button::after {
  position: absolute;
