  }
}

.menu .sub-menu li {
  width: 100%;
}

@media (min-width: 992px) {
  .menu .sub-menu {
    display: block !important;
  }
}

.menu li {
  position: relative;
}

.menu li:hover > .sub-menu {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.menu li.has-sub-menu > a {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.menu li.has-sub-menu > a::after {
  position: absolute;
  font-weight: 900;
  font-size: 16px;
  font-family: "Line Awesome Free";
  content: "\f107";
  right: 0;
  top: 50%;
  transform: translate(8px, -50%);
  font-size: 14px;
}

.menu li a {
  display: block;
  padding: 4px 15px;
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  color: #fff;
  position: relative;
}

.menu li a .badge {
  position: absolute;
  font-weight: 600;
  top: -12px;
  right: -8px;
  font-size: 9px;
  color: #1f1f23 !important;
  border-radius: 3px 3px 3px 0;
  line-height: 0.93;
  padding: 4px 5px 2px;
  z-index: 1;
}

.menu li a .badge::before {
  position: absolute;
  content: "";
  top: calc(100% - 1px);
  left: 0;
  background: #ffc827;
  width: 8px;
  height: 5px;
  clip-path: polygon(0 100%, 0 0, 100% 0);
}

.menu .btn-close {
  position: absolute;
  right: 20px;
  top: 20px;
}

@media (max-width: 1399px) {
  .menu li a {
    font-size: 15px;
    padding: 4px 10px;
  }
}

@media (max-width: 991px) {
  .menu li a {
    font-size: 14px;
    padding: 4px 10px;
  }
}

@media (min-width: 992px) {
  .sub-menu {
    position: absolute;
    top: 100%;
    right: 0px;
    opacity: 0;
    visibility: hidden;
    min-width: 230px;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    transition: all ease 0.3s;
    transform: translateY(15px);
    box-shadow: 0 3px 12px 3px rgba(255, 82, 1, 0.1);
    overflow: hidden;
    z-index: 11;
    background: #350b2d;
    border: 2px solid rgba(255, 200, 39, 0.4);
    padding: 10px;
  }

  .sub-menu li {
    padding: 0;
  }

  .sub-menu li:last-child {
    border: none;
  }

  .sub-menu li a {
    font-size: 14px;
    color: #fff;
    padding: 7px 15px;
    display: block;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-line-clamp: 1;
  }

  .sub-menu li a:hover {
    padding-left: 20px;
    background: #ffc827;
    color: #1f1f23;
  }

  .sub-menu:hover {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }

  .sub-menu .sub-menu {
    visibility: hidden;
    opacity: 0;
    top: 5px;
    left: -100%;
    transform: translateY(20px);
  }

  .sub-menu.has-sub-menu > a::after {
    transform: translate(-20px, -50%);
  }
}

@media (max-width: 991px) {
  .menu > li:nth-last-child(1) {
    border-bottom: none;
  }

  .menu > li:nth-last-child(2) {
    border-bottom: none;
  }

  .menu > li:nth-last-child(3) {
    border-bottom: none;
  }

  .menu {
    position: absolute;
    top: 0;
    left: 0;
    padding: 50px 30px 50px;
    max-height: calc(100vh - 50px);
    min-width: 200px;
    width: 100%;
    visibility: hidden;
    transform-origin: top;
    transform: translateY(-100px) scaleY(0.6);
    opacity: 0;
    overflow-y: auto;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    transition: all ease 0.3s;
  }

  .menu.active {
    background: #2e0327;
    opacity: 1;
    transform: translateY(0) scaleY(1);
    visibility: visible;
    z-index: 9999;
    top: 0px;
  }

  .menu.active .cmn--btn {
    border-color: #fff;
  }

  .menu .has-sub-menu {
    margin-right: 0;
  }

  .menu .sub-menu {
    background: #2e0327;
  }

  .menu .has-sub-menu > a::after {
    transform: translate(-20px, -50%) !important;
  }

  .menu li {
    padding: 3px 0;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }

  .menu li:nth-last-child(1) {
    border-bottom: none;
  }

  .menu li a {
    color: #fff;
    display: inline-block;
  }

  .menu li.has-sub-menu a::after {
    transform: translate(-20px, -50%) !important;
  }

  .menu .btn {
    background: #fff !important;
    border-color: transparent !important;
    color: #ffc827 !important;
  }

  .sub-menu {
    display: none;
    padding-left: 25px;
    background: #fff;
  }
}

