.roll {
  margin-bottom: 100px;
  min-height: 30px;
}
.roll:hover .roll__three-d-box,
.roll:focus .roll__three-d-box {
  transform: translateZ(-25px) rotateX(90deg);
}
.roll__three-d-box {
  transition: all 0.3s ease-out;
  transform: translateZ(-25px);
  transform-style: preserve-3d;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
}
.roll__three-d-box--front {
  transform: rotatex(0deg) translatez(14px);
}
.roll__three-d-box--back {
  transform: rotatex(-90deg) translatez(14px);
  background-color: #c0392b !important;
}
.roll:hover ~ div {
  transform: scale(0.7);
}
.roll:hover ~ div > div {
  animation-duration: 0.5s;
}
.scene {
  width: 150px;
  height: 150px;
  perspective: 600px;
  transition: 1.5s;
}
.scene:after {
  width: 250px;
  height: 30px;
  background: #c0392b;
  border-radius: 50%;
  filter: blur(40px);
  position: relative;
  top: 112px;
  left: 35px;
  z-index: 50;
  opacity: 0.4;
}
.scene input {
  display: none;
}
.scene input:checked + div {
  animation-play-state: paused;
  animation-duration: 0.5s;
}
.scene .cube {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transform: translateZ(-100px);
  animation: roll 8s linear infinite alternate;
}
.scene .cube__face {
  display: grid;
  grid-template: repeat(3, 45px) / repeat(3, 45px);
  border: 1px solid #2e0327;
  background: #4b0e40;
  position: absolute;
  width: 200px;
  height: 200px;
  padding: 32.5px;
}
.scene .cube__face--front i::after {
  background: #e74c3c;
}
.scene .cube__face--back i::after {
  background: #1abc9c;
}
.scene .cube__face--top i::after {
  background: #2980b9;
}
.scene .cube__face--bottom i::after {
  background: #f1c40f;
}
.scene .cube__face--right i::after {
  background: #27ae60;
}
.scene .cube__face--left i::after {
  background: #9b59b6;
}
.scene .cube__face--front {
  transform: rotateY(0deg) translateZ(100px);
}
.scene .cube__face--back {
  transform: rotateY(90deg) translateZ(100px);
}
.scene .cube__face--left {
  transform: rotateY(180deg) translateZ(100px);
}
.scene .cube__face--right {
  transform: rotateY(-90deg) translateZ(100px);
}
.scene .cube__face--top {
  transform: rotateX(90deg) translateZ(100px);
}
.scene .cube__face--bottom {
  transform: rotateX(-90deg) translateZ(100px);
}
.scene .cube__face i {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
.scene .cube__face i:after {
  width: 25px;
  height: 25px;
  border-radius: 50%;
}
